*,
*::after,
*::before {
  box-sizing: border-box;
}
.document {
  scroll-behavior: smooth;
  position: relative;
  height: 100%;
  padding: 0;
  margin: 0;
}
.page {
  position: relative;
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
  min-height: 100%;
  padding: 0;
  margin: 0;
  font-family: "Coolvetica", "Arial", sans-serif;
}
.page__background {
  position: fixed;
  inset: 0;
  background-image: url("../img/shape.svg");
  background-repeat: round;
  background-size: 60px 130px;
  background-position: center;
  z-index: -1;
}
.page__blop-wrapper {
  position: absolute;
  overflow: hidden;
  min-height: 100vh;
  width: 100vw;
}
.page__blob {
  position: absolute;
  inset: 0;
  max-height: fit-content;
  z-index: -1;
}
@media (min-width: 768px) and (max-width: 1170px) {
  .page__blob {
    transform: scale(1.7, 1) translate(-300px, -300px);
  }
}
@media (max-width: 767px) {
  .page__blob {
    transform: scale(2.5) translate(0, 0);
  }
}
@font-face {
  font-family: "Coolvetica";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/Coolvetica.woff2") format("woff2"), url("../fonts/Coolvetica.woff") format("woff");
}
@keyframes letter {
  0% {
    transform: scale(1, 1);
  }
  25% {
    transform: scale(0.8, 1.2);
  }
  50% {
    transform: scale(1.3, 0.7);
  }
  75% {
    transform: scale(0.9, 1.1);
  }
  100% {
    transform: scale(1, 1);
  }
}
@keyframes nav-link {
  0% {
    transform: scale(1, 1);
  }
  25% {
    transform: scale(0.8, 1.2);
  }
  50% {
    transform: scale(1.3, 0.7);
  }
  75% {
    transform: scale(0.9, 1.1);
  }
  100% {
    transform: scale(1, 1);
  }
}
@keyframes text {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.header {
  height: 100vh;
  transition: 1s;
}
@media (max-height: 600px) {
  .header {
    height: max-content;
  }
}
.header__logo {
  color: #ffffff;
  transition: 0.3s;
}
@media (min-width: 768px) {
  .header__logo {
    color: #000c24;
  }
}
.header__logo:hover {
  animation-name: letter;
  animation-duration: 0.6s;
  animation-timing-function: cubic-bezier(0.47, 0.82, 0.17, 1);
}
.header__logo:active {
  opacity: 0.3;
}
.letter {
  display: inline-block;
  transition: 0.3s;
}
.letter:hover {
  color: #003fbd;
}
.navigation {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 40px 100px;
}
@media (max-width: 767px) {
  .navigation {
    padding: 20px 40px;
  }
}
.navigation__toggler {
  display: none;
  width: 40px;
  height: 36px;
  z-index: 2;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
@media (max-width: 767px) {
  .navigation__toggler {
    display: block;
  }
}
.navigation__toggler-line {
  position: relative;
  display: block;
  width: 30px;
  height: 3px;
  background-color: #ffffff;
  border-radius: 4px;
  transition: 0.3s;
}
.navigation__toggler-line::before {
  position: absolute;
  top: 8px;
  display: block;
  width: 30px;
  height: 3px;
  background-color: #ffffff;
  border-radius: 4px;
  content: "";
  transition: 0.3s;
}
.navigation__toggler-line::after {
  position: absolute;
  top: -8px;
  display: block;
  width: 30px;
  height: 3px;
  background-color: #ffffff;
  border-radius: 4px;
  content: "";
  transition: 0.3s;
}
.navigation__toggler--active .navigation__toggler-line {
  transform: translate3d(-100px, 0, 0);
}
.navigation__toggler--active .navigation__toggler-line::before {
  transform: translate3d(100px, -8px, 0) rotate(45deg);
}
.navigation__toggler--active .navigation__toggler-line::after {
  transform: translate3d(100px, 8px, 0) rotate(-45deg);
}
.navigation__list {
  display: flex;
  padding: 0;
  margin: 0;
  list-style-type: none;
  transition: 0.3s;
}
@media (max-width: 767px) {
  .navigation__list {
    position: absolute;
    top: 0;
    left: -100vw;
    height: 100vh;
    width: 50vw;
    padding-top: 100px;
    padding-left: 60px;
    flex-direction: column;
    background-color: #000c24;
    z-index: 1;
  }
}
.navigation__list--active {
  top: 0;
  left: 0;
}
.navigation__item:not(:last-of-type) {
  margin-right: 50px;
}
@media (max-width: 767px) {
  .navigation__item:not(:last-of-type) {
    margin-right: 0;
    margin-bottom: 50px;
  }
}
.navigation__link {
  color: #ffffff;
  text-decoration: none;
  opacity: 0.6;
  transition: 0.3s;
}
@media (max-width: 767px) {
  .navigation__link {
    color: #ffffff;
  }
}
.navigation__link:hover {
  opacity: 1;
  text-shadow: 0 0 20px #ffffff;
}
.navigation__link--active {
  text-shadow: 0 0 10px #000c24;
  z-index: 1;
  opacity: 1;
}
@media (max-width: 767px) {
  .navigation__link--active {
    color: #ffffff;
    text-shadow: 0 0 10px #ffffff;
  }
}
.navigation__toggler:hover .navigation__toggler-line {
  box-shadow: 0 0 10px #ffffff;
}
.navigation__toggler:hover .navigation__toggler-line::before {
  box-shadow: 0 0 10px #ffffff;
}
.navigation__toggler:hover .navigation__toggler-line::after {
  box-shadow: 0 0 10px #ffffff;
}
.hero {
  padding-left: 100px;
  padding-top: 40px;
}
@media (max-width: 767px) {
  .hero {
    padding-left: 40px;
    padding-top: 20px;
  }
}
.hero__title {
  margin: 0;
  color: #ffffff;
  font-size: 60px;
  letter-spacing: -5px;
  cursor: default;
}
@media (max-width: 767px) {
  .hero__title {
    font-size: 40px;
  }
}
.hero__letter {
  position: relative;
  transform: scale(0);
  transition: 0.3s;
  cursor: default;
}
.hero__letter:last-of-type::after {
  position: absolute;
  right: -7px;
  top: -2px;
  display: inline-block;
  width: 2px;
  height: 75px;
  background-color: #ffffff;
  animation-name: text;
  animation-duration: 1s;
  animation-iteration-count: infinite;
  animation-timing-function: step-end;
  content: "";
}
@media (max-width: 767px) {
  .hero__letter:last-of-type::after {
    height: 50px;
  }
}
.hero__letter--decorated::after {
  position: absolute;
  right: -8px;
  top: -2px;
  display: inline-block;
  width: 2px;
  height: 75px;
  background-color: #000c24;
  content: "";
}
@media (max-width: 767px) {
  .hero__letter--decorated::after {
    height: 50px;
  }
}
.hero__description {
  margin: 40px 0;
  color: #ffffff;
  font-size: 18px;
  transform: translateY(200px);
  transition: 0.6s;
  opacity: 0;
}
.hero__social-list {
  display: flex;
  padding: 0;
  list-style-type: none;
  transform: translateY(200px);
  transition: 0.6s;
  opacity: 0;
}
.hero__social-item:not(:last-child) {
  margin-right: 30px;
}
.hero__social-button {
  color: #ffffff;
  transition: 0.3s;
}
.hero__social-button:hover {
  color: #003fbd;
}
.hero__social-button:active {
  opacity: 0.6;
}
.animated {
  animation-name: letter;
  animation-duration: 0.5s;
  animation-timing-function: cubic-bezier(0.47, 0.82, 0.17, 1);
  animation-fill-mode: forwards;
}
.main {
  position: relative;
  flex-grow: 1;
}
.main__up-btn {
  position: fixed;
  bottom: 60px;
  right: 40px;
  display: none;
  background-color: transparent;
  border: none;
  border-radius: 50%;
  z-index: 2;
  cursor: pointer;
  transition: 0.3s;
}
.main__up-btn:hover {
  opacity: 0.6;
}
@media (max-width: 767px) {
  .main__up-btn {
    right: 20px;
  }
}
.portfolio-item {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  height: 100vh;
  padding: 28vh 10vw;
  color: #000c24;
  gap: 40px;
}
@media (max-height: 600px) {
  .portfolio-item {
    height: max-content;
  }
}
@media (max-width: 767px) {
  .portfolio-item {
    grid-template-columns: 1fr;
    padding: 100px 0;
  }
}
@media (min-width: 768px) and (max-width: 1100px) {
  .portfolio-item {
    padding: 28vh 60px;
  }
}
.portfolio-item--odd {
  color: #ffffff;
  background-color: #000c24;
  box-shadow: 0 0 50px 10px #002e8a;
}
.portfolio-item__text-wrapper {
  display: flex;
  padding: 0 40px;
  flex-direction: column;
  grid-column: 1;
  align-items: flex-start;
  transition: 0.8s;
}
@media (min-width: 768px) and (max-width: 1100px) {
  .portfolio-item__text-wrapper {
    padding-right: 0;
  }
}
@media (max-width: 980px) and (min-width: 767px) {
  .portfolio-item__text-wrapper {
    padding: 0;
  }
}
.portfolio-item__title {
  font-size: 50px;
  margin: 0;
  text-transform: capitalize;
  text-decoration: none;
  color: inherit;
  transform: skewX(45deg) translateX(-250px);
  transition: 0.8s;
  opacity: 0;
}
.portfolio-item__title::first-letter {
  color: #003fbd;
}
@media (max-width: 767px) {
  .portfolio-item__title {
    transform: skewX(0) translateX(0) translateY(200px);
  }
}
.portfolio-item__description {
  font-size: 18px;
  letter-spacing: 0.1em;
  transform: skewX(-80deg) translateX(-450px);
  transition: 0.8s;
  opacity: 0;
}
@media (max-width: 767px) {
  .portfolio-item__description {
    transform: skewX(0) translateX(0) translateY(200px);
  }
}
.portfolio-item__buttons-list {
  display: flex;
  padding: 0;
  list-style-type: none;
  transform: translateX(-450px);
  transition: 0.8s;
  opacity: 0;
}
@media (max-width: 767px) {
  .portfolio-item__buttons-list {
    transform: translateX(0) translateY(200px);
  }
}
.portfolio-item__button-item:not(:last-of-type) {
  margin-right: 40px;
}
.portfolio-item__button {
  display: block;
  color: inherit;
  transition: 0.3s;
}
.portfolio-item__button:hover {
  color: #003fbd;
}
.portfolio-item__button:active {
  opacity: 0.6;
}
.portfolio-item__image-wrapper {
  display: grid;
  align-items: end;
  grid-template-columns: repeat(8, 55px);
  transform: translateX(1000px) skewX(-45deg);
  transition: 0.7s;
  grid-row: 1;
  grid-column: 2;
  opacity: 0;
}
@media (max-width: 767px) {
  .portfolio-item__image-wrapper {
    justify-self: center;
    transform: translateY(300px) skewX(0);
    opacity: 0;
    grid-template-columns: repeat(12, 30px);
    grid-column: 1;
  }
}
.portfolio-item__picture {
  align-self: flex-end;
  transition: 0.3s;
}
.portfolio-item__picture--mobile {
  width: max-content;
  grid-column-start: 1;
  grid-row-end: -1;
  z-index: 1;
}
@media (max-width: 980px) and (min-width: 767px) {
  .portfolio-item__picture--mobile {
    grid-column-start: 2;
  }
}
.portfolio-item__picture--desktop {
  width: max-content;
  grid-column-start: 2;
  grid-row-end: -1;
  z-index: -1;
}
@media (max-width: 980px) and (min-width: 767px) {
  .portfolio-item__picture--desktop {
    grid-column-start: 1;
  }
}
.portfolio-item__picture--tablet {
  width: max-content;
  grid-column-start: 9;
  grid-row-end: -1;
  z-index: 1;
}
@media (max-width: 980px) and (min-width: 767px) {
  .portfolio-item__picture--tablet {
    grid-column-start: 5;
  }
}
.portfolio-item__image {
  display: block;
  transition: 0.3s;
}
@media (max-width: 767px) {
  .portfolio-item__image--mobile {
    width: 60px;
    height: auto;
  }
}
@media (max-width: 767px) {
  .portfolio-item__image--desktop {
    width: 300px;
    height: auto;
  }
}
@media (max-width: 767px) {
  .portfolio-item__image--tablet {
    width: 120px;
    height: auto;
  }
}
.portfolio-item--odd .portfolio-item__text-wrapper {
  grid-column: 2;
}
@media (min-width: 768px) {
  .portfolio-item--odd .portfolio-item__text-wrapper {
    align-items: end;
  }
}
@media (max-width: 767px) {
  .portfolio-item--odd .portfolio-item__text-wrapper {
    grid-column: 1;
  }
}
.portfolio-item--odd .portfolio-item__image-wrapper {
  grid-column: 1;
  transform: translateX(-1000px) skewX(45deg);
  opacity: 0;
}
@media (max-width: 767px) {
  .portfolio-item--odd .portfolio-item__image-wrapper {
    transform: translateY(300px) skewX(0);
  }
}
.portfolio-item--odd .portfolio-item__title {
  transform: skewX(-45deg) translateX(250px);
  opacity: 0;
  text-align: right;
}
@media (max-width: 767px) {
  .portfolio-item--odd .portfolio-item__title {
    transform: skewX(0) translateX(0) translateY(200px);
    text-align: left;
  }
}
.portfolio-item--odd .portfolio-item__description {
  text-align: right;
  transform: translateX(450px);
}
@media (max-width: 767px) {
  .portfolio-item--odd .portfolio-item__description {
    transform: translateX(0) translateY(200px);
    text-align: left;
  }
}
.portfolio-item--odd .portfolio-item__buttons-list {
  transform: translateX(450px);
  opacity: 0;
}
@media (max-width: 767px) {
  .portfolio-item--odd .portfolio-item__buttons-list {
    transform: translateX(0) translateY(200px);
  }
}
.skills {
  position: relative;
  min-height: 50vh;
  padding: 5vh 10vw;
  color: #000c24;
}
@media (max-height: 600px) {
  .skills {
    height: max-content;
  }
}
@media (max-width: 767px) {
  .skills {
    grid-template-columns: 1fr;
    margin-top: -100px;
    padding: 40px;
    padding-bottom: 80px;
  }
}
@media (min-width: 768px) and (max-width: 1100px) {
  .skills {
    padding: 2vh 60px;
  }
}
.skills__title {
  font-size: 50px;
  margin: 0;
  margin-bottom: 40px;
  text-transform: capitalize;
  text-decoration: none;
  color: inherit;
  transform: skewX(45deg) translateX(-250px);
  transition: 0.8s;
  opacity: 0;
}
@media (max-width: 767px) {
  .skills__title {
    transform: skewX(0) translateX(0) translateY(200px);
  }
}
.skills__list {
  display: flex;
  padding: 0;
  margin: 0;
  flex-direction: column;
  list-style-type: none;
}
.skills__item {
  display: grid;
  margin: 20px 0;
  grid-template-columns: 100px minmax(300px, 800px);
  align-items: center;
  transform: translateX(-250px);
  transition: 0.8s;
  opacity: 0;
}
@media (max-width: 767px) {
  .skills__item {
    grid-template-columns: 80px minmax(220px, 500px);
    transform: translateX(0) translateY(200px);
  }
}
.skills__name {
  margin: 0;
  font-size: 36px;
  text-transform: uppercase;
  color: rgba(0, 12, 36, 0.75);
}
@media (max-width: 767px) {
  .skills__name {
    font-size: 24px;
  }
}
.skills__range {
  position: relative;
  display: block;
  height: 60px;
  border: 5px solid #000c24;
  border-radius: 30px;
}
@media (max-width: 767px) {
  .skills__range {
    height: 40px;
  }
}
.skills__value {
  max-width: calc(100% - 4px);
  position: absolute;
  left: 2px;
  top: 2px;
  height: 46px;
  border-radius: 30px;
  background-color: #000c24;
  transition: 1.6s;
}
@media (max-width: 767px) {
  .skills__value {
    height: 26px;
  }
}
.skills__value--html {
  width: 0;
}
.skills__value--css {
  width: 0;
}
.skills__value--js {
  width: 0;
}
.html-academy {
  color: inherit;
}
.footer {
  padding: 8px 40px;
  background-color: #ffffff;
  z-index: 1;
}
.footer__social {
  display: grid;
  padding: 0;
  margin: 0;
  grid-template-columns: repeat(4, 40px);
  list-style-type: none;
  justify-items: center;
}
.footer__social-button {
  display: flex;
  color: #000c24;
  transition: 0.3s;
}
.footer__social-button:hover {
  color: #003fbd;
}
